import { ArrowUpRightIcon, DurationIcon, HeartFilledIcon, HeartIcon, TimeIcon, ViewIcon } from "../assets/icons";
import { Box, Center, Flex, HStack, Heading, Icon, Img, Text, useColorModeValue, useToast } from "@chakra-ui/react";

import formatDuration from "../lib/formatDuration";
import redableTime from "../lib/readableTime";
import { useNavigate } from "react-router-dom";
import { useState } from "react";

export default function VideoCard({ single_img, title, uploaded, file_code, views, canplay, length }) {
    const [liked, setLiked] = useState(false);
    const color = useColorModeValue("black", "white");
    const navigate = useNavigate();
    const toast = useToast({ position: "top" });

    const _new = new Date(uploaded) > new Date(new Date() - 1000 * 60 * 60 * 24);

    const onOpen = () => {
        if (canplay) navigate("/video/" + file_code);
        else {
            toast({
                title: "Video is not ready yet",
            });
        }
    };

    return (
        <Center py={6} data-aos="fade-up">
            <Box w="xs" rounded={"sm"} my={5} mx={[0, 5]} overflow={"hidden"} bg={useColorModeValue("white", "black.800")} border={"1px"} borderColor="black.900" shadow={"base"}>
                <Box h={"200px"} borderBottom={"1px"} borderColor="black">
                    <Img src={single_img} roundedTop={"sm"} objectFit="cover" h="full" w="full" alt={title} />
                </Box>
                <Box p={4}>
                    <HStack spacing={4}>
                        {_new && (
                            <Box bg={"black"} display={"inline-block"} px={2} py={1} mb={2} color={"white"}>
                                <Text fontSize={"xs"} fontWeight="medium">
                                    New
                                </Text>
                            </Box>
                        )}

                        <Box bg="black" display={"inline-block"} px={2} py={1} mb={2} color={"white"}>
                            <Text fontSize={"xs"} fontWeight="medium">
                                {title.split(" ")[0]}
                            </Text>
                        </Box>
                    </HStack>

                    <Heading color={color} fontSize={"2xl"} noOfLines={3}>
                        {title}
                    </Heading>
                    <HStack mt={2} justify={"space-between"} opacity={0.5}>
                        <HStack>
                            <Icon as={ViewIcon} ></Icon>
                            <Text fontSize={"sm"} >
                                {views}
                            </Text>
                        </HStack>
                        <HStack>
                            <Icon as={DurationIcon}></Icon>
                            <Text fontSize={"sm"} >
                                {formatDuration(length)}
                            </Text>
                        </HStack>
                        <HStack>
                            <Icon as={TimeIcon}></Icon>
                            <Text fontSize={"sm"} >
                                {redableTime(uploaded)}
                            </Text>
                        </HStack>
                    </HStack>
                </Box>
                <HStack borderTop={"1px solid black"}>
                    <Flex p={4} alignItems="center" justifyContent={"space-between"} roundedBottom={"sm"} cursor={"pointer"} w="full" onClick={onOpen}>
                        <Text fontSize={"md"} fontWeight={"semibold"} color={"blue.400"}>
                            {canplay ? "Open Video" : "Encoding"}
                        </Text>
                        <Icon as={ArrowUpRightIcon} color={"blue.400"} />
                    </Flex>
                    <Flex
                        p={4}
                        alignItems="center"
                        justifyContent={"space-between"}
                        roundedBottom={"sm"}
                        borderLeft={"1px solid black"}
                        cursor="pointer"
                        onClick={() => setLiked(!liked)}
                    >
                        <Icon as={liked ? HeartFilledIcon : HeartIcon} color={"red.500"} fontSize={34} />
                    </Flex>
                </HStack>
            </Box>
        </Center>
    );
}
