import { SimpleGrid, VStack } from "@chakra-ui/react";

import VideoCard from "./VideoCard";

function VideoList({files}) {
    return (
        <VStack>
            <SimpleGrid columns={[1, 2, 2, 3]}>
                {files.map((file) => (
                    <VideoCard key={file.file_code} {...file} />
                ))}
            </SimpleGrid>
        </VStack>
    );
}

export default VideoList;
