import { Button, Card, CardBody, CardFooter, Container, HStack, Heading, Icon, Image, Stack, Text } from "@chakra-ui/react";
import { DurationIcon, FileIcon, TimeIcon, ViewIcon } from "../assets/icons";
import { Link, useParams } from "react-router-dom";

import Loading from "../components/Loading";
import Tags from "./Tags";
import config from "../config";
import fetcher from "../lib/fetcher";
import formatDuration from "../lib/formatDuration";
import readableSize from "../lib/readableSize";
import redableTime from "../lib/readableTime";
import useSWR from "swr";

function Video() {
    const { id } = useParams();

    const { data, error } = useSWR(config.api.info + "?file_code=" + id, fetcher);

    if (error) return <div>{error.message}</div>;
    if (!data) return <Loading />;

    return (
        <Container maxW="container.lg" py={5}>
            <Card direction={{ base: "column" }} variant="outline">
                <Image
                    as={"iframe"}
                    objectFit="cover"
                    w={"100%"}
                    h={["200px", "75vh"]}
                    allowFullScreen={true}
                    scrolling="no"
                    borderRadius={"md"}
                    src={config.DOOD_BASE_URL + data.protected_embed}
                    alt={data.title}
                />
                <CardBody>
                    <Stack mt="6" spacing="3">
                        <Heading>{data.title}</Heading>
                        <Stack fontWeight={"semibold"} fontSize={"2xl"}>
                            <HStack>
                                <Icon as={FileIcon}></Icon>
                                <Text fontSize={"sm"}>Size: {readableSize(data.size)}</Text>
                            </HStack>
                            <HStack>
                                <Icon as={DurationIcon}></Icon>
                                <Text fontSize={"sm"}>Duration: {formatDuration(data.length)}</Text>
                            </HStack>
                            <HStack>
                                <Icon as={TimeIcon}></Icon>
                                <Text fontSize={"sm"}>Date: {redableTime(data.uploaded)}</Text>
                            </HStack>
                            <HStack>
                                <Icon as={ViewIcon}></Icon>
                                <Text fontSize={"sm"}>Views: {data.views}</Text>
                            </HStack>
                        </Stack>
                    </Stack>
                </CardBody>

                <CardFooter>
                    <Button as={Link} variant="solid" colorScheme="blue" to={config.DOOD_BASE_URL + data.protected_dl}>
                        Download
                    </Button>
                </CardFooter>
            </Card>
            <Tags defaultTag={data.title.split(" ")[0]} />
        </Container>
    );
}

export default Video;
