import { Heading, VStack } from "@chakra-ui/react";

import Loading from "../components/Loading";
import Pagination from "../components/Pagination";
import VideoList from "../components/VideoList";
import config from "../config";
import fetcher from "../lib/fetcher";
import useSWR from "swr";
import { useSearchParams } from "react-router-dom";

function Home() {
    const [searchParams, setSearchParams] = useSearchParams();
    const { data, error } = useSWR(config.api.list + "?" + searchParams.toString(), fetcher);

    if (error) return <div>{error.message}</div>;
    if (!data) return <Loading />;

    const setPage = (page) => {
        searchParams.set("page", page);
        setSearchParams(searchParams);
        window.scrollTo({
            top: 0,
            behavior: "smooth",
        });
    };

    return (
        <VStack m={5} size={"sm"}>
            <Heading textAlign={"center"} size={"lg"}>
                Latest Videos
            </Heading>
            <VideoList files={data.files} />
            <Pagination
                currentPage={searchParams.has("page") ? parseInt(searchParams.get("page")) : 1}
                totalCount={data.results_total}
                pageSize={searchParams.has("per_page") ? parseInt(searchParams.get("per_page")) : 20}
                onPageChange={setPage}
            />
        </VStack>
    );
}

export default Home;
